/* ====================== */
/* FONTS */
/* ====================== */

@font-face {
  font-family: AmazonEmberDisplayHeavy;
  src: url("../fonts/AmazonEmberDisplay_He.otf") format("opentype");
}

@font-face {
  font-family: AmazonEmber;
  src: url("../fonts/AmazonEmber_Rg.otf") format("opentype");
}

@font-face {
  font-family: AmazonEmberMono;
  src: url("../fonts/AmazonEmberMono_Rg.otf") format("opentype");
}

@font-face {
  font-family: AmazonEmberDisplayLight;
  src: url("../fonts/AmazonEmberDisplay_Lt.otf") format("opentype");
}

/* ====================== */
/* GLOBAL RESETS */
/* ====================== */
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}
html,
body {
  margin: 0;
  padding: 0;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
fieldset,
img {
  border: 0;
}
input {
  border: 1px solid #b0b0b0;
  padding: 3px 5px 4px;
  color: #979797;
  width: 190px;
}
address,
caption,
cite,
code,
dfn,
th,
var {
  font-style: normal;
  font-weight: normal;
}
ol,
ul {
  list-style: none;
}
caption,
th {
  text-align: left;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}
q:before,
q:after {
  content: "";
}
abbr,
acronym {
  border: 0;
}

$squid-ink: #232f3e;

/* ====================== */
/* MLU RESETS */
/* ====================== */
// Make header smaller
.wbo-header {
  padding-top: 4px;
  padding-bottom: 1px;
}

.wbo-nav li a {
  line-height: auto;
}

footer {
  height: auto;
}

/* ====================== */
/* VARIABLES */
/* ====================== */

html {
  font-size: 16px;
}

body {
  margin: 0;
  -webkit-font-smoothing: subpixel-antialiased;
  -webkit-font-smoothing: antialiased;
  color: var(--content);
  background: linear-gradient(
      217deg,
      rgba(247, 152, 128, 0.35),
      rgba(245, 241, 241, 0) 35.71%
    ),
    linear-gradient(
      0deg,
      rgba(247, 152, 128, 0.24),
      rgba(245, 241, 241, 0) 10.71%
    );
  line-height: 1.5;
}

// colors
/* ====================== */
/* GENERAL */
/* ====================== */
* {
  font-family: AmazonEmberDisplayLight;
  color: $squid-ink;
}

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.bold {
  font-weight: bold;
  font-family: AmazonEmber;
}

/* ====================== */
/* INTRO */
/* ====================== */

#intro {
  display: flex;
  margin: auto;
  text-align: center;
  align-items: center;
  padding-bottom: 1rem;
  min-height: 70vh;
  padding-left: 5%;
}

#intro-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-columns: 55% 42%;
  grid-template-rows: 350px;
  column-gap: 1rem;
  max-width: 85%;
  margin: auto;
  align-items: center;
  max-height: 100vh;

  p {
    font-size: 21px;
    line-height: 29px;
    letter-spacing: 0.1px;
  }
}

#image-container {
  margin-top: 10%;
  height: 110%;
  align-items: center;
  display: flex;
  justify-content: center;
}

img#intro-image {
  vertical-align: middle;
  max-height: 100%;
}

#intro-text {
  text-align: left;
}

title {
  font-family: AmazonEmberDisplayHeavy;
}

main {
  padding-bottom: 2%;
  display: block;
  margin: auto;
  max-width: 1500px;

  h1#title {
    font-family: AmazonemberDisplayHeavy;
    font-size: 3.5rem;
    margin-bottom: 0;
  }
}

.intro-icon-container {
  align-items: center;
}

#ai {
  font-family: AmazonEmberDisplayHeavy;
  color: #fc6a49;
}

#mlu-acronym {
  color: #fc6a49;
  font-weight: bold;
}

#mlu-link {
  text-decoration-thickness: 8%;
}

#mlu-link:hover {
  text-decoration-thickness: 10%;
}

.heavy {
  font-weight: bold;
}

#intro__icon h2 {
  font-family: AmazonEmberDisplayHeavy;
  font-size: 3.2rem;
}

.subtitle {
  font-size: 1.4rem;
  font-weight: bold;
  max-width: 95%;
}

.text {
  h1 {
    font-size: 2.5rem;
    font-family: AmazonEmberDisplayHeavy;
  }
}
.section-segue {
  font-size: 1.5rem;
  width: 100%;
  font-family: AmazonEmberDisplayLight;
  background-size: 100% 10px;
  text-align: left;
}

#articles-section {
  padding-left: 5%;
  max-width: 85%;
  margin: auto;
  padding-top: 4rem;
}

button.content-button {
  font-family: AmazonEmberDisplayLight;
  font-style: normal;
  font-size: 1.5rem;
  color: $squid-ink;
  background-color: rgba(1, 1, 1, 0);
  cursor: pointer;
  z-index: 1;
  position: relative;
  margin: 10px 0px;
  border-radius: 0px;
  padding: 4px 12px;
  border: 1px solid $squid-ink;
  text-align: left;
}

button.content-button::before {
  content: "";
  background-color: rgba(252, 106, 73, 0.55);
  width: 100%;
  height: 1.8rem;
  position: absolute;
  z-index: -1;
  left: -0.25rem;
  top: 0.3rem;
  right: -1rem;
  padding: 0px 0.25rem;
}

button.content-button:hover {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 4px 8px 0px,
    rgba(0, 0, 0, 0.19) 0px 5px 10px 0px;
}

button.content-button > a {
  text-decoration: none;
}

/* ====================== */
/* ARTICLES */
/* ====================== */

.articles-container {
  position: relative;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: center;
  padding: 20px;
  column-gap: 5rem;
  row-gap: 5rem;
  margin: auto;
  width: 100%;

  .article-card {
    position: relative;
    display: flex;
    flex-direction: column;
    text-align: left;

    .imgBx {
      position: relative;
      width: 300px; // was 260
      width: 100%;

      img {
        max-width: 100%;
        width: 342px;
        width: 100%;
        height: 200px;
        border: 0.5px solid $squid-ink;
      }
    }

    .article-content {
      position: relative;
      margin-top: -43px;
      padding: 10px 15px;
      text-align: center;
      color: #111;

      p {
        font-size: 1rem;
        padding-bottom: 5px;
      }
    }
  }
}

.article-title {
  font-size: 32px;
  font-weight: bold;
  visibility: visible;
  font-family: AmazonEmberDisplayHeavy;
  color: $squid-ink;
  margin-bottom: 1rem;
}

.article-description {
  font-size: 21px;
  line-height: 29px;
  letter-spacing: 0.1px;
  margin-bottom: 1rem;
}

.articles-container .article-card:hover {
  opacity: 1;

  .content p {
    opacity: 1;
  }

  .article-title {
    opacity: 1;
  }
}

.icon,
.text {
  vertical-align: middle;
  display: inline-block;
}

@media (max-width: 1250px) {
  .subtitle {
    display: block;
    font-size: 1.3rem;
    max-width: 100%;
  }

  #intro-container {
    // display: grid;
    // grid-template-columns: repeat(2, 1fr);
    // grid-template-columns: 55% 42%;
    // grid-template-rows: 350px;
    column-gap: 1rem;
    max-width: 95%;
  }

  #image-container {
    margin-top: 10%;
    height: 98%;
  }
}

@media (max-width: 950px) {
  .subtitle {
    font-size: 1.15rem;
    // color: red;
    width: 95%;
  }

  #intro-container {
    // display: grid;
    // grid-template-columns: repeat(2, 1fr);
    grid-template-columns: 60% 40%;
    // grid-template-rows: 350px;
    column-gap: 1rem;
    max-width: 95%;
  }

  #image-container {
    margin-top: 10%;
    height: 70%;
    // width: 80%;
  }
}

@media (max-width: 769px) {
  #intro {
    padding-bottom: 1rem;
    min-height: 80vh;
    // padding-top: 4rem;
  }

  #intro-container {
    grid-template-columns: 62% 38%;
    column-gap: 0.5rem;
    max-width: 96%;
  }

  #image-container {
    margin-top: 10%;
    height: 30%;
    // width: 80%;
  }
}

@media (max-width: 606px) {
  .text {
    h1 {
      font-size: 1.85rem;
      font-family: AmazonEmberDisplayHeavy;
    }
  }

  .subtitle {
    font-size: 1.1rem;
    // color: red;
    width: 95%;
  }

  #intro-container {
    grid-template-columns: 100%;
    grid-template-rows: 100%;
    // column-gap: 1rem;
    max-width: 100%;
  }

  img#intro-image {
    display: none;
  }

  #image-container {
    margin-top: 10%;
    height: 40%;
    // width: 80%;
  }

  text {
    h1 {
      font-size: 2rem;
    }
  }

  .section-segue {
    // font-size: 1.3rem;
  }

  #articles-section {
    padding: 0;
    max-width: 95%;
  }

  .articles-container {
    grid-template-columns: 100%;
    padding: 1rem;
    column-gap: 5rem;

    .article-card {
      position: relative;
      display: flex;
      flex-direction: column;
      text-align: left;

      .imgBx {
        img {
          width: 100%;
          height: 150px;
          border: 2px solid $squid-ink;
        }
      }
    }

    .article-title {
      font-size: 1.8rem;
      font-weight: bold;
      visibility: visible;
      font-family: AmazonEmberDisplayHeavy;
      color: $squid-ink;
      margin-bottom: 1rem;
    }

    .article-description {
      font-size: 21px;
      line-height: 29px;
      letter-spacing: 0.1px;
      margin-bottom: 1rem;
    }
  }
}
